import React,{Component} from 'react'
/*import axios from 'axios'*/
import im1 from './images/im1.png'



const styles={
	float:{
		float:'left',
		padding:'10px'
	
	},

	buttonTab:{
		backgroundColor: 'rgba(240,240,240,.5)',
    padding: '5px',
    textAlign: 'center',
    float:'left',marginBottom:'40px',
    zIndex:'1'
	},

	sideProfileShadow:{
		    boxShadow: '2px 0px 18px rgba(211, 211, 211, 0.43)'
}
	



}


class DashBoard extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {
	  	data:'s',
	  	showNews:true,
	  	refresh:'m'

	};

	this.show=this.show.bind(this)
	this.refresh=this.refresh.bind(this)
	}


	componentDidMount() {
/*		axios.get('https://gist.githubusercontent.com/nikethsingh/32b226fdcc15ce09e8083b15d5698d83/raw/e1784b0bb993f332abc53f9862e36ecefa78e05c/news.json')
  .then( (response)=> {
    console.log(response.data.articles);
    console.log(response.data.articles[0].author)
    this.setState({
    	data:response.data.articles,
    	
    })
  })
  .catch(function (error) {
    console.log(error);
  });
  */

  var ds=JSON.parse(localStorage.getItem('newsData'))
  this.setState({data:ds})
 /* console.log(ds)*/
	}


show(){
	/*console.log("hwlo")*/
	this.setState({
		showNews:!this.state.showNews
	})
}

refresh(){/*
	console.log("trying to refresh ............")*/
	var ds=JSON.parse(localStorage.getItem('newsData'))

	this.setState({
		refresh:'n',
		data:ds

	})
}


	render(){

		if(this.state.data==="s")


		 {return(
  				<div className="loader"> </div>

		 	)
		 }
		 else

		{
		return(

			<div className="container-fluid">


				<div className="row">

				<div className="col-lg-3 col-md-3 col-sm-3" style={styles.sideProfileShadow}>
					<SideProfile />
				</div>

				<div className="col-lg-9 col-md-9 col-sm-9">
					<div className="col-lg-12 col-md-12 col-sm-12">
						 <div className="jumbotron">
						    <h1>Welcome , {localStorage.name}</h1> 
						 
						  </div>
					</div>

					<div className="col-lg-12 col-md-12 col-sm-12" style={{zIndex:'0'}}>
						<div className="col-lg-6 col-md-6 col-sm-6" style={styles.buttonTab}>
							<button className="dsh-btn" onClick={this.show}>News</button>

							</div>

						<div className="col-lg-6 col-md-6 col-sm-6" style={styles.buttonTab}>
							<button className="dsh-btn" onClick={this.show}>Add News</button>

						</div>

{this.state.showNews?
						(this.state.data.map((news)=>
						
												<NewsBox
												key={news.index}
												index={news.index}
												newsImage={news.urlToImage}
												newsTitle={news.title}
												refresh={this.refresh}
												/>
												))

						:


						<AddNews refresh={this.refresh}/>
					}


						





					</div>
				</div>

				</div>


</div>
			





			)
	}
	}
}


export default DashBoard;


class NewsBox extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {};

	  this.removeNews=this.removeNews.bind(this)
	}

	removeNews(index){

  var ds=JSON.parse(localStorage.getItem('newsData'))

/*
console.log("indexOF ====>  "+ ds.indexOf("index") )*/

const toDelete = new Set([index]);
const newArray = ds.filter(obj => !toDelete.has(obj.index));

/*console.log(newArray)*/

  var news=ds[index]
/*console.log(news)*/
var new_arr = ds.filter(item => item !== news)

/*console.log(new_arr)*/

   localStorage.setItem('newsData',JSON.stringify(newArray))
let new_ds=JSON.parse(localStorage.getItem('newsData'))
/*console.log(new_ds)*/


this.props.refresh()

	}

	render(){
		return(
	<div className="col-lg-12 col-md-12 col-sm-12"  style={{zIndex :'0'}}>

							<div className="col-lg-9 col-md-9 col-sm-9"style={styles.float} >
								<div className="col-lg-3 col-md-3 col-sm-3" style={styles.float}>
									<img className="admin-news-img" src={this.props.newsImage} alt="pic" />
								</div>

								<div className="col-lg-9 col-md-9 col-sm-9" style={styles.float}>
									{this.props.newsTitle}
								</div>

							</div>
							<div className="col-lg-3 col-md-3 col-sm-3" style={styles.float}>
								<button 
								onClick={()=>this.removeNews(this.props.index)}
								className="dsh-news-remove"

								>remove</button>
							</div>

						</div>



			)
	}
}



class AddNews extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {loadedImage:false};
	  this.showImage=this.showImage.bind(this)
	  this.addnews=this.addnews.bind(this)
	}


addnews(){

let title=this.refs.title.value
let desc=this.refs.description.value
let image=this.state.loadedImage
let delay=250

let ds=JSON.parse(localStorage.getItem('newsData'))


let id=localStorage.id*1;
/*console.log(id)*/
var arrOfNumber = JSON.parse(localStorage.getItem('arrOfNumber') );

var valueOfIndex=arrOfNumber[id]
/*
console.log(arrOfNumber+"value => \n"+valueOfIndex)*/


/*
let valueOfIndex=Math.floor(Math.random() * 30) + 10
console.log(valueOfIndex)*/

console.log(desc)

let adding_news={
	title:title,
	description:desc,
	index:valueOfIndex,
	urlToImage:image,
	author:"",
	publishedAt:"",
	delay:delay

}

console.log(adding_news)
ds.push(adding_news)
   localStorage.setItem('newsData',JSON.stringify(ds))
let new_ds=JSON.parse(localStorage.getItem('newsData'))
console.log(new_ds)
/*console.log(title+"\n"+desc+"\n"+this.state.loadedImage)*/
++id;
localStorage.setItem('id',JSON.stringify(id))

this.props.refresh()

alert("News Posted")
this.refs.title.value=""


}


	showImage(input){
/*
		console.log(input.target.files[0])*/
		let size=input.target.files[0].size

		if(size>555000){

			alert("please upload image of size not more than 500 KB")
		}
		else
		   {  if (input.target.files && input.target.files[0]) {
		                   let reader = new FileReader();
		                   reader.onload = (e) => {
		                       this.setState({loadedImage: e.target.result});
		                   }
		                   console.log(reader.readAsDataURL(input.target.files[0]));
		               }
		               }    
	}

componentDidMount(){
	localStorage.setItem('id',"0")

var arrOfNumber=Array.from({length: 20}, () => Math.floor(Math.random() * 20)+10);
localStorage.setItem('arrOfNumber',JSON.stringify(arrOfNumber))
}

	render(){
		return(
			<div className="col-lg-12 col-md-12 col-sm-12" style={{zIndex :'0'}}>

			  <div className="col-lg-12 col-md-12 col-sm-12"  style={{marginBottom:'25px'}}>
				<div className="col-lg-2 col-md-2 col-sm-2">
					<h5>Add Title</h5>
				</div>

				<div className="col-lg-10 col-md-10 col-sm-10">
					<input ref="title" className="news-title" required type="text" />
				</div>
			  </div>

			  <div className="col-lg-12 col-md-12 col-sm-12" style={{marginBottom:'25px'}}>
				<div className="col-lg-2 col-md-2 col-sm-2">
					<h5>Description</h5>
				</div>

				<div className="col-lg-10 col-md-10 col-sm-10">
					<textarea ref="description" className="news-desc" required cols="50">
					
					</textarea>

				</div>
			  </div>

			  <div className="col-lg-12 col-md-12 col-sm-12" style={{marginBottom:'25px'}}>
				<div className="col-lg-2 col-md-2 col-sm-2">
					{/*Image*/}
				</div>

				<div className="col-lg-10 col-md-10 col-sm-10">
				<input type="file" name="file" id="file" className="inputfile" 
				onChange={(e)=>this.showImage(e)}
				accept="image/*"
				/>
			<label 	htmlFor="file">Choose a image to upload</label>
			{/*	<input type="file" className="news-upload-image"
			 	 onChange={(e)=>this.showImage(e)}
				/>*/}

				{this.state.loadedImage?
					<div><br/><img className="loadedImage" src={this.state.loadedImage} alt="pics"/></div>
					:false}
				</div>
			  </div>

			  <div className="col-lg-12 col-md-12 col-sm-12">
			  	<button onClick={this.addnews} className="post-news">POST</button>
			  </div>



			</div>

			)
	}
}


class SideProfile extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {edit:false,profilePic:im1};
	  this.editFunc=this.editFunc.bind(this)
	  this.saveData=this.saveData.bind(this)
	  this.changeProfileImage=this.changeProfileImage.bind(this)
	}

editFunc(){
	this.setState({
		edit:!this.state.edit
	})
}

changeProfileImage(input){
	     if (input.target.files && input.target.files[0]) {
            let reader = new FileReader();
            reader.onload = (e) => {
                this.setState({profilePic: e.target.result});
            }
            console.log(reader.readAsDataURL(input.target.files[0]));
        }

}

saveData(){
	localStorage.setItem('name', this.refs.name.value)
	localStorage.setItem('email', this.refs.email.value)

	this.setState({
		edit:false
	})


}


	render(){
		return(
			<div>
				<div className="col-lg-12 col-md-12 col-sm-12">

				<img src={this.state.profilePic} className="profile-pic" alt="picss"/>
				<br/>
				<br/>
{this.state.edit ? 

(
	<div><input type="file"  name="file" id="file" accept="image/*"
	 className="inputfile" 
	onChange={(e)=>this.changeProfileImage(e)}/>
	
				<label 	htmlFor="file">change image</label></div>
			)	
:
false


}
				</div>


				<div className="col-lg-12 col-md-12 col-sm-12" style={{padding:'0px'}}>


				<div className="col-lg-10 col-md-10 col-sm-10" >&emsp;</div>


				<div className="col-lg-2 col-md-2 col-sm-2 edit-btn"
				 onClick={this.editFunc}>Edit</div>

<br/><br/>
{this.state.edit ?


				<div className="col-lg-12 col-md-12 col-sm-12 " style={{padding:'0',marginTop:'10px'}}>


				<div className="col-lg-12 col-md-12 col-sm-12" style={{padding:'0'}}>
				<input ref="name" type="text" className="editName" defaultValue={localStorage.name} />
				</div>

				<div className="col-lg-12 col-md-12 col-sm-12" style={{padding:'0'}}>
				<input ref="email" type="email" className="editEmail" 

				defaultValue={localStorage.email}/>
				</div>

<div className="col-lg-12 col-md-12 col-sm-12" style={{textAlign:'center'}}>
			<button onClick={this.saveData} className="edit-save">save</button>
				</div>

				</div>


	:

				<div className="col-lg-12 col-md-12 col-sm-12 editCard">

				<div className="col-lg-12 col-md-12 col-sm-12 dis-name">
					{localStorage.name==undefined?(<p>admin</p>):(<p>{localStorage.name}</p>)}
				</div>

				<div className="col-lg-12 col-md-12 col-sm-12 dis-email">
				{localStorage.email==undefined?(<p>admin@gmail.com</p>):(<p>{localStorage.email}</p>)}
				</div>


				

				</div>

}




				</div>

			</div>



			)
	}
}


