import React,{Component} from 'react'


const styles={
	float:{
		float:'left',
		padding:'0px'
	}
}

export default class NewsCard extends Component{
	render(){
		return(
			<div className={this.props.className}>
				<div className="col-lg-6 col-md-6 col-sm-6" style={styles.float}>
					<img src={this.props.newsPic} alt="news-im" />
				</div>

				<div className="col-lg-6 col-md-6 col-sm-6" style={styles.float}>
					<p className="newsTitle"> {this.props.newsTitle}</p>
				</div>


			</div>


			)
	}
}


