import React,{Component} from 'react'
import NewsCard from './NewsCard'
import logo from './images/logo.webp'
import './main.css'
import axios from 'axios'
import {
 withRouter,
  Link
} from 'react-router-dom'

import { Zoom } from 'react-reveal';



/*import Halogen from 'halogen'*/


 class HomePage extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {
	  	data:"s",
	  	index:"-1"
	  };

	  this.loadNewPage=this.loadNewPage.bind(this)
	}

	componentDidMount() {
   var ds=JSON.parse(localStorage.getItem('newsData'))
 if(!!ds){
    this.setState({
    	data:ds,
    	/*index:response.data.index*/
    })
}
else

	{
		axios.get('https://gist.githubusercontent.com/nikethsingh/32b226fdcc15ce09e8083b15d5698d83/raw/e1784b0bb993f332abc53f9862e36ecefa78e05c/news.json')
	  .then( (response)=> {
	  /*  console.log(response.data.articles);*/
	   /* console.log(response.data.articles[0].author)*/
	   localStorage.setItem('newsData',JSON.stringify(response.data.articles))
	   var ds=JSON.parse(localStorage.getItem('newsData'))
	
	  /* console.log(ds)*/
	   if(!!ds){}
	    this.setState({
	    	data:ds,
	    	/*index:response.data.index*/
	    })
	  })
	  .catch(function (error) {
	    console.log(error);
	  });
	}
}


	loadNewPage(index){
		/*console.log(index)*/

		
	this.props.history.push(`/news/${index}`);
}

filter(e){
/*	console.log(e.target.value)*/
  this.setState({
  	filter:e.target.value
  })
}

	render(){


		

		if(this.state.data==="s")


		 {return(
  				<div className="loader"> </div>

		 	)
		 }
		 else

		{
			let items=this.state.data
		
			if(this.state.filter)
			{
				items=items.filter( item =>
					item.title.toLowerCase()
					.includes(this.state.filter.toLowerCase())
					)

			/*	console.log(items)*/
			}

				return(
					<div>
						<div className="container-fluid">
							<div className="row" 
							style={{marginBottom:'4rem',marginTop :'.5rem'}}>
							  <div className="col-lg-2 col-md-2 col-sm-2">
<img src={logo} alt="logo" style={{height:'auto'}}/>
							  </div>
							  <div className="col-lg-8 col-md-8 col-sm-8">
							  	<input type="search" onChange={this.filter.bind(this)}/>
		
							  </div>
							  <div className="col-lg-2 col-md-2 col-sm-2">
							  	<button className="button">
							  	{/*<span className="fa fa-user"></span>*/}
							  <Link to="/dashboard">	Admin</Link>

							  </button>
							  </div>
		
		
							</div>
		
							<div className="row" >
		
							{items.map((data)=>
		
								  
								<Zoom 
								delay={data.delay*1} 
								key={data.index} 
								className=" newsCard col-lg-4 col-md-4 col-sm-4" 
								style={{float:'left',zIndex:'1'}}
								
								>
									<div onClick={()=>this.loadNewPage(data.index)}>
								    <NewsCard 
								     className="wrapper"
								      newsPic={data.urlToImage} 
								      newsTitle={data.title}
									/>
									</div>
								
		
		</Zoom>
		
		
								)}
								
		
									{/*<div className=" newsCard col-lg-4 col-md-4 col-sm-4">
								    <NewsCard 
								      newsPic={im1} 
								      newsTitle="som news title here with some cool stuff"
									/>
								</div>*/}
		
								</div>
							</div>
					</div>
				
		
		
		
		
			)
			}
		}
	}

export default withRouter(HomePage)


