import React,{Component} from 'react'

export default class Login extends Component{
	constructor(props) {
	  super(props);
	
	  this.state = {};

	  this.validate=this.validate.bind(this)
	}


validate(){
	var user=this.refs.name.value
	var pass=this.refs.pass.value
	 
	 if(user==='niketh'&&pass==='nikethsingh'){
	 		this.props.history.push("/");
	 	console.log(user)
	 }

	 else{
	 	this.props.history.push("/login");
	 }
}

	render(){
		return(

			<div className="login-form">

			<form>
			  <div>
				<label className="user-label">username</label>
				<input ref="name" className="user-name" type="text" />
			  </div>

			  <div>
				<label className="user-label">password</label>
				<input ref="pass" className="user-email" type="text" />
			  </div>

			  <div>
			
				<button className="user-submit"  onClick={this.validate}> Submit</button>
			  </div>


			</form>


			</div>


			)
	}
}


